'use client';
import React, { Suspense } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import { Login } from '@/modules/Login';

function LoginContent() {
    const router = useRouter();
    const searchParams = useSearchParams();

    const contextParam = searchParams.get('context');
    // Default to tenant login if not specified
    const context = (contextParam === 'tenant' || contextParam === 'admin') ? contextParam : 'tenant';
    const salonId = searchParams.get('salonId') || undefined;
    const prefilledEmail = searchParams.get('email') || undefined;

    const handleLogin = (sid?: string, isProfessional?: boolean, professionalId?: string) => {
        if (context === 'tenant' && sid) {
            if (isProfessional && professionalId) {
                router.push(`/dashboard/pro/${sid}/${professionalId}`);
            } else {
                router.push(`/dashboard/tenant/${sid}`);
            }
        } else {
            router.push('/dashboard/admin');
        }
    };

    const handleClientLogin = (phone: string) => {
        router.push(`/portal?phone=${phone}`);
    };

    return (
        <Login
            context={context}
            salonId={salonId}
            prefilledEmail={prefilledEmail}
            onLogin={handleLogin}
            onClientLogin={handleClientLogin}
            onBack={() => router.push('/register')}
            onRegister={() => router.push('/register')}
        />
    );
}

export default function LoginPage() {
    return (
        <Suspense fallback={<div className="min-h-screen flex items-center justify-center">Carregando...</div>}>
            <LoginContent />
        </Suspense>
    );
}
